import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Injector,
  Input,
  OnInit,
  Output,
  Renderer2,
  ViewChild
} from '@angular/core';
import {ControlValueAccessor, FormControl, Validator} from '@angular/forms';
import {animate, AnimationEvent, keyframes, style, transition, trigger, useAnimation} from '@angular/animations';
import {isNil} from 'lodash-es';
import {DOWN_ARROW, UP_ARROW} from '@angular/cdk/keycodes';

import {
  AdaptDeviceDetectionService, AdaptRadarAngularCustomEventName,
  AdaptRxControlLabelTooltip,
  AdaptTooltipDirective,
  AdaptTranslateService,
  ANIMATION_DURATION,
  ANIMATION_NAME,
  ANIMATION_TIMING_FUNCTION,
  CounterOperations,
  createFormControlValidator,
  createFormControlValueAccessor,
  formatString,
  FormComponent,
  getEventKeyCode,
  isUndefined,
  KeyValueObject,
  ValidationError
} from '@bmc-ux/adapt-angular';
import {AdaptRadarService} from '@bmc-ux/adapt-radar';


// TODO: Refactor this if possible.
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
declare function require(str: any): any;

// eslint-disable-next-line @typescript-eslint/no-var-requires
const bigNumber = require('bignumber.js');

@Component({
  selector: 'adapt-counter',
  templateUrl: './counter.component.html',
  providers: [
    createFormControlValueAccessor(AdaptCounterComponent),
    createFormControlValidator(AdaptCounterComponent)
  ],
  animations: [
    trigger('highlightMobileIncrease', [
      transition(':enter', [
        style({bottom: 0, opacity: 0}),
        animate(ANIMATION_DURATION.fast + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionInOut,
          style({bottom: 'calc(100% + 10px)', opacity: 1})
        )
      ]),
      transition(':leave', [
        style({bottom: 'calc(100% + 10px)', opacity: 1}),
        animate('.12s',
          style({bottom: 0, opacity: 0})
        )
      ])
    ]),
    trigger('highlightMobileDecrease', [
      transition(':enter', [
        style({top: 0, opacity: 0}),
        animate(ANIMATION_DURATION.fast + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionInOut,
          style({top: 'calc(100% + 10px)', opacity: 1})
        )
      ]),
      transition(':leave', [
        style({top: 'calc(100% + 10px)', opacity: 1}),
        animate('.12s',
          style({top: 0, opacity: 0})
        )
      ])
    ]),
    trigger('validationFeedbackAnimation', [
      transition(':enter', [
        useAnimation(ANIMATION_NAME.fadeIn)
      ]),
      transition(':leave', [
        useAnimation(ANIMATION_NAME.fadeOut)
      ])
    ]),
    trigger('errorToggleAnimation', [
      transition(':enter', [
        style({
          opacity: 0
        }),
        animate(250)
      ]),
      transition(':leave', [
        style({
          opacity: 1
        }),
        // animate(150)
      ])
    ]),
    trigger('highlightArrow', [
      transition('0 => 1',
        animate(ANIMATION_DURATION.medium,
          keyframes([
            style({color: '*', offset: 0}),
            style({color: '#00867E', offset: 1})
          ]))
      )
    ])
  ]
})
export class AdaptCounterComponent extends FormComponent implements ControlValueAccessor, Validator, AfterViewInit, OnInit {
  static id: number = 0;
  // Need for using inherited properties in the template
  context: AdaptCounterComponent = this;
  pattern: RegExp;
  counterValue: number | string;
  animateArrowUp: boolean = false;
  animateArrowDown: boolean = false;
  isCompact: boolean = false;
  increaseAnimationDone: boolean = false;
  decreaseAnimationDone: boolean = false;
  focused: boolean = false;
  isMobile: boolean = false;
  isSpinnerMobile: boolean = false;
  precision: number;
  defaultErrors:
    {
      increase?: string;
      decrease?: string;
      rangeName?: string;
      range?: string;
      invalidName?: string;
      invalid?: string;
    } = {};
  /**
   * Label icon with a tooltip or popover
   */
  tooltipConfig: AdaptRxControlLabelTooltip = null;

  @Input() id: string;
  @Input() testID: string;
  @Input() name: string;
  @Input() prefix: string;
  @Input() suffix: string;
  @Input() label: string = '';

  @Input()
  set max(value: number | string) {
    this._max = this._isSafeNumber(value) ? parseFloat(<string>value) : bigNumber(value);
  }

  get max(): number | string {
    return !!this._max || this._max === 0 ? this._max : Number.MAX_SAFE_INTEGER;
  }

  @Input()
  set min(value: number | string) {
    this._min = this._isSafeNumber(value) ? parseFloat(<string>value) : bigNumber(value);
  }

  get min(): number | string {
    return !!this._min || this._min === 0 ? this._min : Number.MIN_SAFE_INTEGER;
  }

  @Input() allowScientific: boolean = true;
  @Input() step: number = 1;
  @Input() width: string = '100%';
  @Input() formControl: FormControl;
  @Input() size: string;
  @Input() preventInnerValidation: boolean = false;

  @Input()
  set allowIntegerOnly(value: boolean) {
    this._integerOnly = this.allowScientific ? false : value;
  }

  get allowIntegerOnly(): boolean {
    return this._integerOnly;
  }

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('aria-describedby') ariaDescribedBy: string | null = null;

  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly focus: EventEmitter<void> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly blur: EventEmitter<void> = new EventEmitter();
  @Output() readonly valueChanged: EventEmitter<number | string> = new EventEmitter();

  @ViewChild('container', {static: true}) containerEl: ElementRef<HTMLDivElement>;
  @ViewChild('input', {static: true}) inputEl: ElementRef<HTMLInputElement>;
  @ViewChild('prefixEl', {static: false}) prefixEl: ElementRef<HTMLSpanElement>;
  @ViewChild('suffixEl', {static: false}) suffixEl: ElementRef<HTMLSpanElement>;
  @ViewChild('mobileActionsContainer', {static: false}) mobileActionsContainer: ElementRef<HTMLElement>;

  @HostBinding('class')
  get hostClasses(): string {
    return [
      this.mainCls,
      'adapt-counter',
      this.isErrorExist ? 'is-error' : '',
      this.suppressIcons ? 'prevent-success-icon' : ''
    ].join(' ');
  }

  get isErrorExist(): boolean {
    return Boolean(this.errors.length);
  }

  get customClass(): string {
    let customCls = '';

    switch (this.size) {
      case 'small':
        customCls = 'form-control-sm';
        break;
      case 'large':
        customCls = 'form-control-lg';
        break;
    }

    return customCls;
  }

  get inputSize(): number {
    return (this._value && this._value.toString().length) || 1;
  }

  get value(): number | string {
    return this._value;
  }

  private get _isTruncatedPrefix(): boolean {
    if (isUndefined(this.prefixEl)) {
      return false;
    }

    const {scrollWidth, offsetWidth} = this.prefixEl.nativeElement;

    return scrollWidth > offsetWidth;
  }

  private get _isTruncatedSuffix(): boolean {
    if (isUndefined(this.suffixEl)) {
      return false;
    }

    const {scrollWidth, offsetWidth} = this.suffixEl.nativeElement;

    return scrollWidth > offsetWidth;
  }

  private _patterns: { scientific: RegExp; normal: RegExp } = {
    scientific: /^-?(\d+|\d+\.\d+|\.\d+)([eE][-+]?\d+)?$/g,
    normal: /^-?(\d+|\d+\.\d+|\.\d+)?$/g
  };
  private _numberPatters: RegExp = /^[+-]?(\d+|\.\d+|\d+\.\d+|\d+\.)(e[+-]?\d+)?$/;
  private _minWidthToMobile: number = 200;
  private _value: number;
  private _integerOnly: boolean;
  private _max: number;
  private _min: number;
  private _isValidNumber: boolean = true;
  private _removeDocumentClickListener: Function;
  private readonly currentLocale: string;

  constructor(private _renderer: Renderer2,
              private _injector: Injector,
              private _deviceDetection: AdaptDeviceDetectionService,
              ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService,
              _elem: ElementRef<HTMLElement>) {
    super(_injector, ts, _adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptCounter);

    this.id = `c-${AdaptCounterComponent.id}`;
    AdaptCounterComponent.id++;
    this.setDefaultTexts(ts.getCurrentLanguage());
    this.currentLocale = ts.getCurrentLocale();
  }

  propagateChange: () => void = (): void => {
    return;
  };

  ngOnInit(): void {
    super.ngOnInit();

    this.mainCls = this.mainCls + ' form-group';
    this.isMobile = this._deviceDetection.isMobile();

    super.initControl();
    this.pattern = this.allowScientific ? this._patterns.scientific : this._patterns.normal;

    if (this.step % 1 !== 0) {
      // If step is not an integer then extract the length of the decimal part
      this.precision = this.step.toString().split(/[.]/)[1].length;
    }

    for (const prop in this._customErrorTexts) {
      if (prop) {
        this.overrideBuildInValidatorMessage(prop, () => this._customErrorTexts[prop]);
      }
    }

    if (this.context.tooltipIconCls && !this.context.popoverIcon) {
      this.tooltipConfig = {
        iconName: this.context.tooltipIconCls,
        content: this.context.tooltipText,
        placement: 'top',
        popoverMode: false
      };
    } else if (this.context.popoverIcon) {
      this.tooltipConfig = {
        iconName: this.context.popoverIcon,
        content: this.popoverContent,
        placement: this.context.popoverPlacement,
        popoverMode: true,
        popoverTriggers: this.context.popoverTriggers
      };
    }
  }

  ngAfterViewInit(): void {
    super.ngAfterViewInit();

    this._renderer.setStyle(this.containerEl.nativeElement, 'width', this.width);
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.defaultErrors.decrease = translation['adapt.counter.decrease'];
    this.defaultErrors.increase = translation['adapt.counter.increase'];
    this.defaultErrors.range = translation['adapt.counter.error.range'];
    this.defaultErrors.rangeName = translation['adapt.counter.error.rangeName'];
    this.defaultErrors.invalid = translation['adapt.counter.error.invalid'];
    this.defaultErrors.invalidName = translation['adapt.counter.error.invalidName'];
  }

  writeValue(value: number | string): void {
    this._setValue(value, false);

    this.counterValue = this._value && this.pattern.test(<string>value) ? value : this._value;
  }

  registerOnChange(fn: () => void): void {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this._onTouchedCallback = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this._disabled = isDisabled;
    this._closeMobileSpinners();
  }

  checkForMobileSpinners(): void {
    if (this.isMobile && !this.disabled) {
      this._openMobileSpinners();
    }
  }

  increaseCounter(animationNeeded?: boolean): void {
    if (this.isMobile) {
      return;
    }

    this._onTouchedCallback();
    this.increase();

    if (animationNeeded) {
      const timer = setTimeout(() => {
        this.animateArrowUp = false;
        clearTimeout(timer);
      }, parseFloat(ANIMATION_DURATION.fast) * 1000);

      this.animateArrowUp = true;
    }
  }

  decreaseCounter(animationNeeded?: boolean): void {
    if (this.isMobile) {
      return;
    }

    this._onTouchedCallback();
    this.decrease();

    if (animationNeeded) {
      const timer = setTimeout(() => {
        this.animateArrowDown = false;
        clearTimeout(timer);
      }, parseFloat(ANIMATION_DURATION.fast) * 1000);

      this.animateArrowDown = true;
    }
  }

  increaseMobile(): void {
    this._onTouchedCallback();
    this.increase();
  }

  decreaseMobile(): void {
    this._onTouchedCallback();
    this.decrease();
  }

  increase(): void {
    const newValue = this.calculateNewValue(CounterOperations.INCREASE);

    if (newValue === null) { // implicit check
      if (this.min < 0 && this.max > 0) {
        this._setValue(0, true);
      } else {
        this._setValue(this.min, true);
      }
    } else if (newValue < this.min) {
      this._setValue(this.min, true);
    } else {
      this._setValue(newValue > this.max ? this.max : newValue, true);
    }

    this.valueChanged.emit(this.value);
    this.counterValue = this.value;
  }

  decrease(): void {
    const newValue = this.calculateNewValue(CounterOperations.DECREASE);

    if (newValue === null) { // implicit check
      if (this.min < 0 && this.max > 0) {
        this._setValue(0, true);
      } else {
        this._setValue(this.max, true);
      }
    } else if (newValue > this.max) {
      this._setValue(this.max, true);
    } else {
      this._setValue(newValue < this.min ? this.min : newValue, true);
    }

    this.valueChanged.emit(this.value);
    this.counterValue = this.value;
  }

  calculateNewValue(operation: CounterOperations): number {
    const counterNumValue = parseFloat(this.counterValue as string);

    if (isNaN(counterNumValue)) {
      return null;
    }

    const valuePrecision = !this.precision && counterNumValue % 1 !== 0
      ? counterNumValue.toString().split(/[.]/)[1].length
      : this.precision;

    switch (operation) {
      case CounterOperations.INCREASE:
        return this._toFixed(counterNumValue + this.step, valuePrecision);

      case CounterOperations.DECREASE:
        return this._toFixed(counterNumValue - this.step, valuePrecision);
    }
  }

  increaseAllowed(): boolean {
    if (isNaN(this.value as number)) {
      return true;
    }

    return isNil(this.value) || this.value < this.max;
  }

  decreaseAllowed(): boolean {
    if (isNaN(this.value as number)) {
      return true;
    }

    return isNil(this.value) || this.value > this.min;
  }

  validate(c: FormControl): KeyValueObject<ValidationError> {
    if (this.preventInnerValidation) {
      return;
    }

    if (!isNil(c.value)) {
      if (!this._isValidNumber) {
        return {
          counterError: this._customErrorTexts.invalidNumberError || {
            name: this.defaultErrors.invalidName,
            text: this.defaultErrors.invalid,
          }
        };
      }

      const hasRangeError = bigNumber.isBigNumber(c.value) ?
        (bigNumber(c.value).lt(this.min) || bigNumber(c.value).gt(this.max)) :
        (c.value < this.min || c.value > this.max) || isNaN(c.value);

      if (hasRangeError) {
        return {
          counterError: this._customErrorTexts.rangeError || {
            name: this.defaultErrors.rangeName,
            text: formatString(this.defaultErrors.range,
              this.min.toLocaleString(this.currentLocale),
              this.max.toLocaleString(this.currentLocale)
            )
          }
        };
      }
    }

    return null;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  onInput(input: any): void {
    this._setValue(input.value, true);
    this.counterValue = input.value;

    this.valueChanged.emit(!!this.value || this.value === 0 ? this.value : null);
  }

  onKeyPress(e: KeyboardEvent): void {
    if (this.disabled || this.readonly) {
      return;
    }

    const regExp: RegExp = new RegExp(this.allowScientific
      ? /^[-+]?\.?[0-9]?[eE]?$/g
      : this.allowIntegerOnly ? /^[-+]?\d?$/g : /^[-+]?\.?[0-9]?$/g);

    const isValidChar = !!regExp.test(e.key);

    if (!isValidChar) {
      e.preventDefault();

      return;
    }
  }

  onFocus(): void {
    this.focused = true;
    this.focus.emit();
  }

  onBlur(): void {
    this.focused = false;
    this.blur.emit();
    this._onTouchedCallback();
    this.propagateChange();
  }

  onKeyDown(e: KeyboardEvent): void {
    if (this.disabled || this.readonly) {
      return;
    }

    const keyCode = getEventKeyCode(e);

    if (keyCode === UP_ARROW || keyCode === DOWN_ARROW) {
      e.preventDefault();

      switch (keyCode) {
        case UP_ARROW: {
          if (this.increaseAllowed()) {
            this.increaseCounter(true);
          }
          break;
        }
        case DOWN_ARROW: {
          if (this.decreaseAllowed()) {
            this.decreaseCounter(true);
          }
          break;
        }
      }
    }
  }

  toggleTooltip(tooltip: AdaptTooltipDirective, el: HTMLElement): void {
    tooltip.delay = 0;

    if (!tooltip.isOpen() && el.offsetWidth >= el.scrollWidth) {
      return;
    }

    if (tooltip.isOpen()) {
      tooltip.close();
    } else {
      tooltip.open();
    }
  }

  onAnimationIncreaseStart(event: AnimationEvent): void {
    if (event.phaseName === 'start') {
      this.increaseAnimationDone = false;
    }
  }

  onAnimationIncreaseDone(event: AnimationEvent): void {
    if (event.phaseName === 'done') {
      this.increaseAnimationDone = true;
    }
  }

  onAnimationDecreaseStart(event: AnimationEvent): void {
    if (event.phaseName === 'start') {
      this.decreaseAnimationDone = false;
    }
  }

  onAnimationDecreaseDone(event: AnimationEvent): void {
    if (event.phaseName === 'done') {
      this.decreaseAnimationDone = true;
    }
  }

  private _setValue(v: string | number, propagateChange: boolean): void {
    if (v === null || v === undefined || v.toString().length === 0) {
      this._value = null;
    } else {
      this._isValidNumber = !!this._numberPatters.test(v.toString());

      this._value = this._isSafeNumber(v) ? parseFloat(<string>v) : bigNumber(<string>v);
    }

    if (propagateChange) {
      this.propagateChange();
    }
  }

  private _onTouchedCallback: () => void = () => {
    return;
  };

  /**
   * Opens the mobile spinners
   */
  private _openMobileSpinners(): void {
    if (!this.isSpinnerMobile) {
      this.isSpinnerMobile = true;

      // Set timeout to skip 'open' click
      setTimeout(() => {
        this._removeDocumentClickListener = this._renderer.listen('document', 'click', (event: Event) => {
          if (!this.mobileActionsContainer.nativeElement.contains(event.target as HTMLElement)) {
            this._closeMobileSpinners();
          }
        });
      }, 0);
    }
  }

  /**
   * Closes the mobile spinners
   */
  private _closeMobileSpinners(): void {
    if (this.isSpinnerMobile) {
      this.isSpinnerMobile = false;
      this._removeDocumentClickListener();
    }
  }

  private _isSafeNumber(value: string | number): boolean {

    // for NaN return true
    if (isNaN(<number>value)) {
      return true;
    }

    // check if passed argument is decimal
    const isDecimal = value && value.toString().indexOf('.') > 0;

    const quotient: string = isDecimal ? value.toString().split(/[.]/)[0] : value.toString();

    if (Number.isSafeInteger(Number(quotient))) {
      if (!isDecimal) {
        return true;
      }

      return !(Number(quotient) === Number.MAX_SAFE_INTEGER || Number(quotient) === Number.MIN_SAFE_INTEGER);
    }

    return false;
  }

  private _toFixed(value: number, precision: number = 0): number {
    const power = Math.pow(10, precision);

    return Math.round(value * power) / power;
  }
}
